@font-face{font-family:"Nunito";font-weight:400;src:url("./Nunito/Nunito-Regular.ttf") format("truetype")}@font-face{font-family:"Nunito";font-weight:700;src:url("./Nunito/Nunito-Bold.ttf") format("truetype")}@font-face{font-family:"Montserrat";font-weight:400;src:url("./Montserrat/Montserrat-Regular.ttf") format("truetype")}@font-face{font-family:"Montserrat";font-weight:500;src:url("./Montserrat/Montserrat-Medium.ttf") format("truetype")}@font-face{font-family:"Montserrat";font-weight:600;src:url("./Montserrat/Montserrat-SemiBold.ttf") format("truetype")}@font-face{font-family:"Montserrat";font-weight:700;src:url("./Montserrat/Montserrat-Bold.ttf") format("truetype")}@font-face{font-family:"Sen";font-weight:700;src:url("./Sen/Sen-Bold.ttf") format("truetype")}.card{position:relative;width:100%;max-width:1110px;height:344px;box-sizing:border-box;background-color:#fff;padding:40px 40px 40px 30px;box-shadow:3px 3px 10px 0px rgba(212,73,0,.1);border-radius:10px;transition:height .3s,padding .3s ease-in-out}.card_unactive{height:180px;padding:20px 30px;display:flex;cursor:pointer;transition:box-shadow .3s,height .3s,padding .3s ease-in-out}.card_unactive_new::before{content:"Добавить новый стикер";position:absolute;top:0;left:0;width:100%;height:100%;display:flex;flex-direction:row;justify-content:center;align-items:center;font-size:22px;font-weight:500;border-radius:10px;background-color:#fff;opacity:.98;transition:opacity .3s,color .35s ease-in-out;z-index:2}.card_unactive_new:hover::before{opacity:.3;color:rgba(0,0,0,0)}.card_unactive:hover{box-shadow:3px 3px 10px 0px rgba(212,73,0,.2)}.category{margin:0;max-width:100px;font-weight:500;font-size:16px;line-height:16px;display:flex;align-items:center}.category .shape_hidden{margin-left:20px;font-size:16px;font-style:normal;font-weight:400;line-height:normal}.text{margin:0;display:flex;flex-direction:row;justify-content:flex-start;align-items:center;gap:10px;font-weight:400;font-size:16px;line-height:16px}.info{display:grid;grid-template-columns:255px minmax(160px, 265px) 1fr;grid-template-rows:repeat(3, min-content);row-gap:35px;column-gap:25px}@media screen and (max-width: 1010px){.info{grid-template-columns:255px repeat(1, 1fr);grid-template-rows:repeat(4, max-content)}}@media screen and (max-width: 845px){.info{grid-template-columns:repeat(2, 1fr)}}.info_unactive{display:grid;grid-template-columns:255px minmax(160px, 265px) 1fr;grid-template-rows:repeat(3, min-content);row-gap:20px;column-gap:30px}.image{grid-row:1/4;margin:0 auto}@media screen and (max-width: 1010px){.image{grid-row:1/1}}@media screen and (max-width: 845px){.image{grid-column:1/3}}.flex{position:relative;padding:0;border:none;display:flex;flex-direction:row;justify-content:flex-start;align-items:flex-start;flex-wrap:wrap;gap:20px}.flexible{display:flex;flex-direction:row;justify-content:flex-start;align-items:center;flex-wrap:wrap;gap:13px}.flex_shapes{grid-column:2/4}@media screen and (max-width: 1010px){.flex_shapes{grid-column:2/3}}@media screen and (max-width: 845px){.flex_shapes{grid-column:1/3}}.shapes{display:flex;flex-wrap:wrap;gap:23px}.options{display:flex;flex-direction:column;gap:10px;font-weight:400;font-size:14px;line-height:16px}.option{display:flex;align-items:center;flex-wrap:wrap;gap:10px}.delete{position:absolute;right:30px;top:40px}.save{position:absolute;right:70px;top:40px;animation:jump .4s linear alternate infinite}.save:disabled{animation:none}@keyframes jump{0%{transform:translate3d(0, 0, 0) scale3d(1, 1, 1)}40%{transform:translate3d(0, -12%, 0) scale3d(0.95, 1.05, 1)}100%{transform:translate3d(0, -30%, 0) scale3d(1, 0.95, 1)}}.add{position:absolute;right:30px;bottom:30px;font-weight:400;font-size:14px;line-height:16px;padding:6px 12px;height:40px}.motion{width:100%}.visible{visibility:visible}.hidden{display:none}.color_sample{width:19px;height:19px;box-shadow:1px 1px 4px 0px rgba(105,37,1,.25);border-radius:3px}.size_hidden{padding:8px;margin-left:18px;font-size:16px;font-style:normal;font-weight:500;line-height:normal;background-color:#eaf1ff;border-radius:10px}