@font-face{font-family:"Nunito";font-weight:400;src:url("./Nunito/Nunito-Regular.ttf") format("truetype")}@font-face{font-family:"Nunito";font-weight:700;src:url("./Nunito/Nunito-Bold.ttf") format("truetype")}@font-face{font-family:"Montserrat";font-weight:400;src:url("./Montserrat/Montserrat-Regular.ttf") format("truetype")}@font-face{font-family:"Montserrat";font-weight:500;src:url("./Montserrat/Montserrat-Medium.ttf") format("truetype")}@font-face{font-family:"Montserrat";font-weight:600;src:url("./Montserrat/Montserrat-SemiBold.ttf") format("truetype")}@font-face{font-family:"Montserrat";font-weight:700;src:url("./Montserrat/Montserrat-Bold.ttf") format("truetype")}@font-face{font-family:"Sen";font-weight:700;src:url("./Sen/Sen-Bold.ttf") format("truetype")}.container{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;gap:25px}@media screen and (max-width: 768px){.container{max-width:287px}}.id{font-weight:500}.content{display:grid;grid-template-columns:1fr minmax(1fr, 250px);grid-template-rows:280px 1fr;align-items:flex-start;gap:20px}@media screen and (max-width: 768px){.content{grid-template-columns:1fr}}.main{width:100%;display:flex;flex-direction:row;justify-content:space-between;align-items:flex-start}@media screen and (max-width: 768px){.main{justify-content:center}}.info{padding:0 0 0 10px;margin:0;display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;gap:10px}.info_item{list-style-type:none;display:flex;flex-direction:row;justify-content:flex-start;align-items:flex-start;gap:10px}.info_item:before{content:"•";color:#1a3263}.buttons{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;gap:10px}.button{padding:10px 12px;height:40px}.button:first-child{margin-top:15px}@media screen and (max-width: 768px){.button:first-child{margin-top:0}}@media screen and (max-width: 768px){.button{width:100%;font-size:16px;padding:8px 12px}}.delivery{min-width:180px;display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;gap:20px;grid-row:1/3;grid-column:2/3}@media screen and (max-width: 768px){.delivery{grid-column:1/2;grid-row:3/4}}.statuses{padding:0;margin:0;display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;gap:20px}.status{list-style:none;display:flex;flex-direction:row;justify-content:flex-start;align-items:flex-start;gap:12px}.status_title{font-size:18px;font-weight:500}.status:before{content:"";border-radius:50%;min-width:9px;height:9px;background-color:#1a3263;margin-top:5px}.flex{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;gap:3px}.date{font-size:14px}